.myItem {
  width: 100%;
  user-select: none; }

.card {
  display: flex;
  align-items: center;
  justify-content: start;
  flex-direction: row;
  box-shadow: 0px 0px 10px 0 rgba(0,0,0,0.3);
  transition: 0.3s;
  margin: 10px 5px 10px 5px;
  background-color: inherit;
  border-radius: 10px;
  padding: 5px; }

.cardShopBlack {
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
  box-shadow: 0px 0px 10px 0 rgba(0,0,0,0.3);
  transition: 0.3s;
  margin: 5px 20px 10px 20px;
  background-color: inherit;
  border-radius: 10px; }

